/* /////////////////// */
/* Overall Styling */
/* /////////////////// */

html, body {
  height: 100%;
  /* overflow: hidden; */
}

[data-scroll-container] {
  min-height: 100vh;
  position: relative;
}


body {
    background-color: var(--black);
}

